// src/gradioApi.js
import { Client } from "@gradio/client";

const GRADIO_URL = "C2MV/PROYECTO_2024"; // Reemplaza con la URL correcta de tu modelo

// Funcin para llamar a la API de Gradio
export async function callGradio(question) {
  try {
    // Conectamos al cliente de Gradio
    console.log("Conectando al cliente de Gradio...");
    const client = await Client.connect(GRADIO_URL);
    console.log("Cliente de Gradio conectado.");

    // Llamada a la prediccin usando los datos requeridos
    console.log("Enviando pregunta a Gradio:", question);
    const result = await client.predict("/on_submit", {
      message: question,
      selected_option: "Ambos (basado en umbral de similitud)",
      similarity_threshold: 0.5,
      system_prompt: `
      Acta como un presentador entusiasta del proyecto 'SpaceRodents' del NASA SPACE APPS Challenge 2024.
      Tu misin es explicar de forma accesible y emocionante cmo un equipo diverso usa inteligencia artificial para analizar datos de ratones en el espacio.
      Comunica los objetivos del proyecto, la tecnologa utilizada y sus posibles beneficios, usando analogas simples y un tono conversacional.
      Muestra pasin por la ciencia espacial mientras explicas cmo estudiamos los efectos del espacio en los roedores.
      Evita jerga tcnica compleja, no actes como investigador y no inventes resultados especficos.
      Conecta el proyecto con beneficios cotidianos y mantn el misterio y la emocin de la exploracin espacial.
      `,
      max_length: 100,
    });

    console.log("Respuesta de Gradio recibida:", result.data); // Log de respuesta
    return result.data; // Retornamos los datos obtenidos
  } catch (error) {
    console.error("Error al llamar a Gradio:", error);
    throw error; // Lanza el error si ocurre algn problema
  }
}
